.Title {
  text-transform: uppercase;
  font-size: 3rem;

  background-image: linear-gradient(to right, #f7ff00, #db36a4);
  -webkit-background-clip: text;
  color: transparent;

  position: relative;
  padding-bottom: 1rem;
  letter-spacing: 3px;
  &::before {
    content: "";
    bottom: 0;
    position: absolute;
    left: 0;
    background-color: #0381ff38;
    width: 6rem;
    height: 0.4rem;
    border-radius: 50px;
  }
  &::after {
    content: "";
    bottom: 0;
    position: absolute;
    left: 0;
    background-color: #037fff;
    width: 3rem;
    height: 0.4rem;
    border-radius: 50px;
  }
  span {
    background-image: linear-gradient(to right, #f7ff00, #db36a4);
    -webkit-background-clip: text;
    color: transparent;

    position: absolute;
    top: 15%;
    left: 0;
    font-size: 5rem;
    opacity: 0.09;
  }
}
