.ServicesSection {
  .service {
    width: 95%;
    background-color: #191d2b;
    border-left: 1px solid #2e344e;
    border-right: 1px solid #2e344e;
    border-bottom: 1px solid #2e344e;
    border-top: 8px solid #2e344e;
    transition: all 0.4s ease-in-out;
    @media screen and (max-width: 1400px) {
      width: 100%;
      margin-bottom: 1rem;
    }
    &:hover {
      border-top: 3px solid;
      border-image-slice: 1;
      border-image-source: linear-gradient(to left, #08ecec, #f3d00c);

      transform: translateY(-1.5rem) scale(1.01);
      box-shadow: 0 1.5rem 4rem rgba(orangered, 0.7);
    }

    .service-content {
      padding: 1rem;
      .s-title {
        font-size: 1.6rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 30%;
          height: 1px;
          background-color: #2e344e;
        }
      }
    }
  }
}

.servives-container {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem;
  gap: 1rem;
  @media screen and (max-width: 700px) {
    flex-direction: column;
    margin-bottom: 2rem;
  }
}
